nav{
  width: 100%;
  margin: 0;
  padding: 0;
  top: 0;
  height: 50px;
  border-bottom: 3px solid black;
  background-color: white;
  position: fixed;
  
}
nav ul{
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
}
nav ul li{
  /* float: left;  */
  display: inline-block; 
  list-style: none;
}
.right-side-nav{
  float: right;
}
nav ul li a{
  display: block;
  padding: 15.8px;
  text-decoration: none;
  color: black;
  font-family: Montserrat-Medium;
  font-weight: 900;  
}
nav ul li a:hover{
  color: white;
  background-color: black;

}
h3, h4 {
  margin-bottom: 0;
}
#page-body{
  padding: 10px 50px 20px;
  display: block;
  margin: 100px 22%;
  max-width: 700px;
  border: 1px solid black;
  border-radius: 10px;
  font-family: Montserrat-Medium;
  
}
.home-heading{
  margin: 0;
  padding: 10px 0;
  font-weight: 700;
  font-family: Montserrat-Medium;
}
.page-body p{
  margin-bottom: 16px;
  
}
.article-list-item{
  text-decoration: none;
  color: black;
}
.btn-upvote{
  padding: 10px;
  margin-right: 10px;
  border-radius: 7px;
  font-family: Montserrat-Medium;
  background-color: black;
  color: white;
  border: solid 2px black;
}
.btn-downvote{
  padding: 10px;
  border: solid 2px black;  
  border-radius: 7px;
  font-family: Montserrat-Medium;
  background-color: black;
  color: white;
}